import React, { useState } from 'react';
import {
  ChakraProvider,
  Box,
  VStack,
  Grid,
  theme,
  Container,
  Heading,
  FormControl,
  FormLabel,
  Input,
  Button,
  useToast,
} from '@chakra-ui/react';
import axios from 'axios';

function App() {
  const [formData, setFormData] = useState({
    host_file: 'snapshot_vmlist.txt',
    chg_number: '',
    ttl_duration: 7,
  });
  const [isLoading, setIsLoading] = useState(false);
  const toast = useToast();

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    try {
      const response = await axios.post('/api/create-snapshots', formData);
      toast({
        title: 'Snapshots created',
        description: `Total VMs: ${response.data.total_vms}, Successful: ${response.data.successful_snapshots}, Failed: ${response.data.failed_snapshots}`,
        status: 'success',
        duration: 9000,
        isClosable: true,
      });
    } catch (error) {
      toast({
        title: 'Error',
        description: error.response?.data?.error || 'An error occurred',
        status: 'error',
        duration: 9000,
        isClosable: true,
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <ChakraProvider theme={theme}>
      <Box textAlign="center" fontSize="xl">
        <Grid minH="100vh" p={3}>
          <VStack spacing={8}>
            <Container maxW="container.md" centerContent>
              <Heading as="h1" size="xl" mb={6}>
                Azure Self-Destruct Snapshot Creator
              </Heading>
              <Box width="100%" p={8} borderWidth={1} borderRadius={8} boxShadow="lg">
                <form onSubmit={handleSubmit}>
                  <VStack spacing={4}>
                    <FormControl id="host_file">
                      <FormLabel>Filename with VM list</FormLabel>
                      <Input
                        type="text"
                        name="host_file"
                        value={formData.host_file}
                        onChange={handleInputChange}
                      />
                    </FormControl>
                    <FormControl id="chg_number">
                      <FormLabel>CHG number</FormLabel>
                      <Input
                        type="text"
                        name="chg_number"
                        value={formData.chg_number}
                        onChange={handleInputChange}
                        placeholder="Enter CHG number"
                        required
                      />
                    </FormControl>
                    <FormControl id="ttl_duration">
                      <FormLabel>Snapshot Time-to-Live (days)</FormLabel>
                      <Input
                        type="number"
                        name="ttl_duration"
                        value={formData.ttl_duration}
                        onChange={handleInputChange}
                        min={1}
                      />
                    </FormControl>
                    <Button
                      mt={4}
                      colorScheme="blue"
                      isLoading={isLoading}
                      type="submit"
                      width="full"
                    >
                      Create Snapshots
                    </Button>
                  </VStack>
                </form>
              </Box>
            </Container>
          </VStack>
        </Grid>
      </Box>
    </ChakraProvider>
  );
}

export default App;